import {
  Box,
  Button,
  Grid,
  IconButton,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import DoneIcon from "@mui/icons-material/Done";
import { Clear } from "@mui/icons-material";
import RewardModal from "./Common/RewardModal";
import RejectReferralModal from "./Common/RejectReferralModal";
import { API_PATHS } from "../utils/apiUtils";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import axios from "axios";
import date from "date-and-time";
import { useNavigate } from "react-router-dom";
import EditIcon from "@mui/icons-material/Edit";

const Transactions = () => {
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [allTransactions, setAllTransactions] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    getAllTransactions();
  }, []);

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const getAllTransactions = async () => {
    try {
      console.log("Url Path-->", API_PATHS.GET_ALL_PENS);

      const response = await axios.get(API_PATHS.GET_ALL_PENS);
      if (response.status === 200) {
        const transaction = response.data.result.map((item, index) => ({
          sno: index + 1,
          databaseId: item._id,
          id: `TRNS${item._id.slice(-5)}`,
          _id: item._id,
          name: item.name,
          color: item.color,
          description: item.description,
          icon: item.icon,
          did_you_know: item.did_you_know,
          file_name: item.file_name ? item.file_name : "NA",
          root_folder_name: item.root_folder_name
            ? item.root_folder_name
            : "NA",
          created_on: date.format(
            new Date(item.created_on),
            "ddd, MMM DD YYYY hh:mm A"
          ),
        }));
        setAllTransactions(transaction);
      }
    } catch (error) {
      console.log("Error In get all referrals-->", error);
    }
  };

  const columns = [
    {
      field: "action",
      headerName: "Action",
      sortable: false,
      renderCell: ({ row }) => (
        <IconButton onClick={() => navigate(`/edit-product/${row._id}`)}>
          <EditIcon color="red" />
        </IconButton>
      ),
    },
    { field: "sno", headerName: "S.No", width: 70 },
    { field: "id", headerName: "ID", width: 100 },
    { field: "name", headerName: "Name", width: 120 },
    { field: "color", headerName: "Color", width: 120 },
    { field: "description", headerName: "Description", width: 200 },
    { field: "icon", headerName: "Icon", width: 250 },
    { field: "did_you_know", headerName: "Did you know", width: 200 },
    { field: "file_name", headerName: "File name", width: 200 },
    { field: "root_folder_name", headerName: "Root folder name", width: 200 },
    { field: "created_on", headerName: "Created on", width: 230 },
  ];

  const exportCSV = (rows, columns) => {
    const csvRows = [];

    // Create the header row
    const exportedColumns = columns.filter((item) => !item.hideInExport);
    const header = exportedColumns.map((column) => column.headerName);
    csvRows.push(header.join(","));

    // Create the data rows
    rows.forEach((row) => {
      const rowData = exportedColumns.map((column) => {
        return row[column.field];
      });
      csvRows.push(rowData.join(","));
    });

    // Combine rows into a single CSV string
    const csvString = csvRows.join("\n");

    // Create a blob containing the CSV data
    const blob = new Blob([csvString], { type: "text/csv" });

    // Create a temporary URL to the blob
    const url = window.URL.createObjectURL(blob);

    // Create a link element to trigger the download
    const a = document.createElement("a");
    a.href = url;
    a.download = "Products_data.csv";

    // Trigger the click event on the link to initiate the download
    a.click();

    // Clean up the temporary URL
    window.URL.revokeObjectURL(url);
  };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000} // Adjust the duration as needed
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      />
      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Products</Typography>
        <div style={{ display: "flex" }}>
          <Button
            variant="contained"
            onClick={() => navigate("/add-product")}
            style={{
              height: "80%",
              backgroundColor: "#ed1b24",
              marginRight: 10,
            }}
          >
            Add Product
          </Button>
          <Button
            variant="contained"
            onClick={() => exportCSV(allTransactions, columns)}
            style={{ height: "80%", backgroundColor: "#ed1b24" }}
          >
            Export To CSV
          </Button>
        </div>
      </Box>
      <Box sx={{ width: "100%" }}>
        <DataGrid
          autoHeight
          rows={allTransactions}
          columns={columns}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          // pageSizeOptions={[10, 10]}
          checkboxSelection
        />
      </Box>
    </Box>
  );
};

export default Transactions;
