const Users = () => {
  const [rows, setRows] = useState([]);
  const navigateTo = useNavigate();

  useEffect(() => {
    getUsers();
  }, []);

  const handleView = (rowData) => {
    navigateTo("/userDetail", { state: { userDetails: rowData } });
  };

  const exportCSV = (rows, columns) => {
    const csvRows = [];

    // Create the header row
    const exportedColumns = columns.filter((item) => !item.hideInExport);
    const header = exportedColumns.map((column) => column.headerName);
    csvRows.push(header.join(","));

    // Create the data rows
    rows.forEach((row) => {
      const rowData = exportedColumns.map((column) => {
        return row[column.field];
      });
      csvRows.push(rowData.join(","));
    });

    // Combine rows into a single CSV string
    const csvString = csvRows.join("\n");

    // Create a blob containing the CSV data
    const blob = new Blob([csvString], { type: "text/csv" });

    // Create a temporary URL to the blob
    const url = window.URL.createObjectURL(blob);

    // Create a link element to trigger the download
    const a = document.createElement("a");
    a.href = url;
    a.download = "Users_data.csv";

    // Trigger the click event on the link to initiate the download
    a.click();

    // Clean up the temporary URL
    window.URL.revokeObjectURL(url);
  };

  const columns = [
    { field: "id", headerName: "ID", width: 100 },
    {
      field: "action",
      headerName: "Actions",
      width: 90,
      hideInExport: true,
      sortable: true,
      renderCell: ({ row }) => {
        return (
          <div style={{ justifyContent: "space-between" }}>
            <RemoveRedEyeIcon
              style={{ color: "#50C878", cursor: "pointer" }}
              onClick={() => handleView(row)}
            />
            {/* <EditIcon
              style={{ color: "#4169E1", cursor: "pointer" }}
              onClick={() => handleEdit(row)}
            ></EditIcon>
            <DeleteIcon style={{ color: "#D22B2B" }}
            onClick={() => handleDelete(row)}></DeleteIcon> */}
          </div>
        );
      },
    },
    { field: "fullName", headerName: "Name", width: 200 },
    { field: "email", headerName: "Email", width: 300 },
    { field: "mobile", headerName: "Mobile No.", width: 150 },
    { field: "aadhar", headerName: "Aadhar", width: 150 },
    { field: "address", headerName: "Address", width: 200 },
    {
      field: "bank_account_holder_name",
      headerName: "Holder Name",
      width: 150,
    },
    { field: "bank_account_number", headerName: "Account No.", width: 150 },
    { field: "bank_name", headerName: "Bank Name", width: 150 },
    { field: "gst_number", headerName: "GST No.", width: 150 },
    { field: "ifsc_code", headerName: "IFSC No.", width: 150 },
    { field: "pan", headerName: "PAN", width: 150 },
  ];

  const getUsers = async () => {
    try {
      console.log("Url Path-->", API_PATHS.GET_ALL_USERS);
      const response = await axios.post(API_PATHS.GET_ALL_USERS);
      const usersArray = response.data.map((item) => {
        return {
          id: item.user_id,
          fullName: item.full_name,
          email: item.email_address,
          mobile: item.mobile_no,
          aadhar: item.aadhar,
          address: item.address,
          bank_account_holder_name: item.bank_account_holder_name,
          bank_account_number: item.bank_account_number,
          bank_name: item.bank_name,
          gst_number: item.gst_number,
          ifsc_code: item.ifsc_code,
          pan: item.pan,
        };
      });
      console.log("Users-->", response.data[20]);
      setRows(usersArray);
    } catch (error) {
      console.log("Error Getting All Users-->", error);
    }
  };

  return (
    <Box sx={{ width: "85%", m: 2 }}>
      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Users</Typography>
        <Button
          variant="contained"
          onClick={() => exportCSV(rows, columns)}
          style={{ height: "80%" }}
        >
          Export To CSV
        </Button>
      </Box>
      <Box sx={{ width: "100%", overflowX: "scroll" }}>
        <DataGrid
          rows={rows}
          columns={columns}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          // pageSizeOptions={[10, 10]}
          checkboxSelection
        />
      </Box>
    </Box>
  );
};

export default Users;
